import React, { Component } from 'react'

class Items extends Component {
  render() {
    const item=this.props.all;
    const theItem=item.map( (it)=>{
        return(
            <div>
                <p>{it.id}</p>
                <p>{it.name}</p>
                <p>{it.age}</p>
            </div>
        )
    })
   
   
    return (
      <div>
        <p>{theItem}</p>
      </div>
    )
  }
}
export default Items;